import config from "config";

export function getUrl(uri: string) {
  const protocol = uri.split(':')[0].toLowerCase()
  let url = '';
  switch (protocol) {
    case 'https':
    case 'http':
      url = uri;
      break
    default: {
      if (uri.indexOf('/') == 0) {
        url = `${config.BASE_URL}${uri}`
      } else {
        url = `${config.BASE_URL}/${uri}`
      }
    }
      break;
  }
  return url;
}
// Example POST method implementation:
export async function postData(uri: string, data = {}): Promise<any> {
  const url = getUrl(uri);
  let response
  try {
    response = await fetch(url, {
      method: 'POST', // *GET, POST, PUT, DELETE, etc.
      mode: 'cors', // no-cors, *cors, same-origin
      cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
      credentials: 'omit', // include, *same-origin, omit
      headers: {
        'Content-Type': 'application/json'
      },
      redirect: 'follow', // manual, *follow, error
      referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
      body: JSON.stringify(data) // body data type must match "Content-Type" header
    });
  } catch (error) {
    console.debug('Failed to fetch list', url, error)
  }
  if (response) {
    if (!response.ok) {
      throw new Error(`Failed to download list ${url}`)
    }
    const json = await response.json()
    return json.result;
  }
  return []
}

// Example GET method implementation:
export async function getData(uri: string): Promise<any> {
  const url = getUrl(uri);
  let response
  try {
    response = await fetch(url);
  } catch (error) {
    console.debug('Failed to fetch list', url, error)
  }
  if (response) {
    if (!response.ok) {
      throw new Error(`Failed to download list ${url}`)
    }
    const json = await response.json()
    return json.result;
  }
  return []
}


