const upColor = '#00da3c';
const downColor = '#ec0000';

function splitData(rawData: (number | string)[][]) {
  const categoryData = [];
  const values = [];
  const volumes = [];
  for (let i = 0; i < rawData.length; i++) {
    const d: any = [];
    let j = 0;
    for (const item of rawData[i]) {
      if (j === 0) d.push(item)
      else d.push(getNumber(item, '0,0.0000000', 6));
      j++;
    }
    const datestr = d.splice(0, 1)[0];
    const date = moment.unix(datestr).format('DD HH:mm')
    categoryData.push(date);

    values.push(d);
    volumes.push([i, d[4], d[0] > d[1] ? 1 : -1]);
  }

  return {
    categoryData: categoryData,
    values: values,
    volumes: volumes
  };
}

function calculateMA(dayCount: number, data: { values: (string | number)[][] }) {
  const result = [];
  for (let i = 0, len = data.values.length; i < len; i++) {
    if (i < dayCount) {
      result.push('-');
      continue;
    }
    let sum = 0;
    for (let j = 0; j < dayCount; j++) {
      const d = data.values[i - j][1] as number;
      sum += d;
    }
    result.push(+(sum / dayCount).toFixed(3));
  }
  return result;
}

