import { Trans } from '@lingui/macro'
import { NetworkAlert } from 'components/NetworkAlert/NetworkAlert'
import NumberText from 'components/NumberText'
import { useActiveWeb3React } from 'hooks/web3'
import moment from 'moment'
import { AddressLink, RecentTransactionsDiv } from 'pages/PoolAnalysis/Components'
import { useEffect } from 'react'
import { ExternalLink as LinkIcon } from 'react-feather'
import { RouteComponentProps } from 'react-router-dom'
import { useRecentTransactions } from 'state/poolAnalysis/hooks'
import { useFetchKListCallback, useFetchRecentTransactionsCallback } from 'state/poolAnalysis/useFetchCallback'
import { shortenAddress } from 'utils'
import { ExplorerDataType, getExplorerLink } from 'utils/getExplorerLink'
import AppBody from '../AppBody'

const Tr = (props: any) => {
    const { chainId } = useActiveWeb3React()
    const { r } = props;
    const [t1, t2] = r.pair_name.split('_');
    let actionName = '-';
    if (r.action == 1) actionName = `Add ${t1} and ${t2}`;
    if (r.action == 2) actionName = `Remove ${t1} and ${t2}`;
    if (r.action == 3) actionName = `Swap ${t1} for ${t2}`;
    if (r.action == 4) actionName = `Swap ${t2} for ${t1}`;
    return <tr key={r.id} style={{ borderBottom: '1px solid red' }}>
        <td style={{ width: '220px', paddingLeft: '10px' }}> <AddressLink
            href={getExplorerLink(chainId || 56, r.tx_hash, ExplorerDataType.TRANSACTION)}
        >
            <span style={{ marginRight: '4px' }}>
                <Trans>{actionName}</Trans>
            </span>
            <LinkIcon size={16} />
        </AddressLink></td>
        <td style={{ width: '150px', paddingLeft: '10px' }}><NumberText num={r.token1_value} format='0,0.000000' /></td>
        <td style={{ width: '150px', paddingLeft: '10px' }}><NumberText num={r.token1_value} format='0,0.000000' /> {t1}</td>
        <td style={{ width: '150px', paddingLeft: '10px' }}><NumberText num={r.token2_value} format='0,0.000000' /> {t2}</td>
        <td style={{ width: '200px', paddingLeft: '10px' }}><AddressLink
            href={getExplorerLink(chainId || 56, r.from, ExplorerDataType.ADDRESS)}
        >
            <span style={{ marginRight: '4px' }}>
                <Trans>{shortenAddress(r.from)}</Trans>
            </span>
            <LinkIcon size={16} />
        </AddressLink></td>
        <td style={{ width: '150px', paddingLeft: '10px' }}>{moment(r.create_time).fromNow()}</td>
    </tr>
}
export default function My({ history }: RouteComponentProps) {
    const d = useRecentTransactions();
    const getKLine = useFetchKListCallback();
    const getTxs = useFetchRecentTransactionsCallback();
    useEffect(() => {
        getTxs(`/api/Fst/recent_transactions?pair_address=0x8161BDCb8c1301bbBD438C472f6da1CAFF56aaF3&limit=500&skip=0`);
    }, [getKLine, getTxs]);

    return (
        <>
            <NetworkAlert />
            <AppBody margin='5rem 16px 1rem 16px'>
                <RecentTransactionsDiv maxHeight={5000}><table style={{ width: '1020px', lineHeight: '40px' }}>
                    <tr style={{ fontSize: '1rem' }}>
                        <th style={{ width: '220px', paddingLeft: '10px' }}>Actions</th>
                        <th style={{ width: '150px', paddingLeft: '10px' }}>Total Value</th>
                        <th style={{ width: '150px', paddingLeft: '10px' }}>Token Amount</th>
                        <th style={{ width: '150px', paddingLeft: '10px' }}>Token Amount</th>
                        <th style={{ width: '200px', paddingLeft: '10px' }}>Account</th>
                        <th style={{ width: '150px', paddingLeft: '10px' }}>Time</th>
                    </tr>
                </table>
                    <div style={{ width: '1020px', lineHeight: '35px', overflow: 'auto', marginBottom: '30px' }}>
                        <table style={{ width: '1020px', lineHeight: '35px', fontSize: '0.8rem' }}>
                            {d.map((p: any) => <Tr key={p.id} r={p} />)}
                        </table>
                    </div>
                </RecentTransactionsDiv>
            </AppBody>
        </>
    )
}
