import { ActionCreatorWithPayload, createAction } from '@reduxjs/toolkit'



export interface KlineQueryParams {
  token0: string
  token1: string
  limit: number
  interval: number
}
export const fetchKList: Readonly<{
  pending: ActionCreatorWithPayload<{ url: string; requestId: string }>
  fulfilled: ActionCreatorWithPayload<{ url: string; kList: []; requestId: string }>
  rejected: ActionCreatorWithPayload<{ url: string; errorMessage: string; requestId: string }>
}> = {
  pending: createAction('poolAnalysis/fetchKList/pending'),
  fulfilled: createAction('poolAnalysis/fetchKList/fulfilled'),
  rejected: createAction('poolAnalysis/fetchKList/rejected'),
}


export const fetchRecentTransactions: Readonly<{
  pending: ActionCreatorWithPayload<{ url: string; requestId: string }>
  fulfilled: ActionCreatorWithPayload<{ url: string; data: []; requestId: string }>
  rejected: ActionCreatorWithPayload<{ url: string; errorMessage: string; requestId: string }>
}> = {
  pending: createAction('poolAnalysis/fetchRecentTransactions/pending'),
  fulfilled: createAction('poolAnalysis/fetchRecentTransactions/fulfilled'),
  rejected: createAction('poolAnalysis/fetchRecentTransactions/rejected'),
}