export const Img = styled.img`
width:100%;
`;

export const Card = (props: { label: string, price: number, changeType: number, rate: number, style?: any }) => {
  const { label, price, rate, style, changeType } = props;
  return <AutoColumn justify="flex-start" style={{ flex: 1, ...style }}>
    <Row>
      <TYPE.small>{label}</TYPE.small>
      <TYPE.small>
        <NumberText
          num={rate}
          style={{
            transform: 'scale(0.9)',
            display: 'block',
            transformOriginX: 'left',
            transformOriginY: 'top',

          }}
          changeType={changeType} a="%"
        />
      </TYPE.small>
    </Row>
    <NumberText style={{ margin: '0.4rem 0' }} num={price} changeType={changeType} changeTypePrefix={['', '', '']} />
    <TYPE.small><TYPE.darkGray>≈￥<NumberText num={price * 6.7} /></TYPE.darkGray></TYPE.small>
  </AutoColumn>
}

