import { nanoid } from '@reduxjs/toolkit'
import { useCallback } from 'react'
import { useAppDispatch } from 'state/hooks'
import { getData } from 'utils/request'
import { fetchKList, fetchRecentTransactions } from './actions'

export function useFetchKListCallback(): (listUrl: string, sendDispatch?: boolean) => Promise<any> {
  const dispatch = useAppDispatch()

  // note: prevent dispatch if using for list search or unsupported list
  return useCallback(
    async (listUrl: string, sendDispatch = true) => {
      const requestId = nanoid()
      sendDispatch && dispatch(fetchKList.pending({ requestId, url: listUrl }))
      return getData(listUrl)
        .then((klist: any) => {
          sendDispatch && dispatch(fetchKList.fulfilled({ url: listUrl, kList: klist, requestId }))
          return klist
        })
        .catch((error: any) => {
          console.debug(`Failed to get list at url ${listUrl}`, error)
          sendDispatch && dispatch(fetchKList.rejected({ url: listUrl, requestId, errorMessage: error.message }))
          throw error
        })
    },
    [dispatch]
  )
}


export function useFetchRecentTransactionsCallback(): (listUrl: string, sendDispatch?: boolean) => Promise<any> {
  const dispatch = useAppDispatch()

  // note: prevent dispatch if using for list search or unsupported list
  return useCallback(
    async (listUrl: string, sendDispatch = true) => {
      const requestId = nanoid()
      sendDispatch && dispatch(fetchRecentTransactions.pending({ requestId, url: listUrl }))
      return getData(listUrl)
        .then((klist: any) => {
          sendDispatch && dispatch(fetchRecentTransactions.fulfilled({ url: listUrl, data: klist, requestId }))
          return klist
        })
        .catch((error: any) => {
          console.debug(`Failed to get list at url ${listUrl}`, error)
          sendDispatch && dispatch(fetchRecentTransactions.rejected({ url: listUrl, requestId, errorMessage: error.message }))
          throw error
        })
    },
    [dispatch]
  )
}

