import { Trans } from '@lingui/macro';
import DoubleCurrencyLogo from 'components/DoubleLogo';
import { NetworkAlert } from 'components/NetworkAlert/NetworkAlert';
import { useCurrency } from 'hooks/Tokens';
import { useMemo, useState } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { WrappedTokenInfo } from 'state/lists/wrappedTokenInfo';
import { useDerivedMintInfo } from '../../state/mint/hooks';
import AppBody from '../AppBody';
import { PoolData, RecentTransactionsDiv, SubTitle, Title } from './Components';
import KChart from './KChart';
import RecentTransactions from './RecentTransactions';



export default function Home({
  match: {
    params: { currencyIdA, currencyIdB },
  } }: RouteComponentProps<{ currencyIdA?: string; currencyIdB?: string }>) {

  const [currencyTab, setCurrencyTab] = useState('history');
  const currencyA = useCurrency(currencyIdA) as WrappedTokenInfo
  const currencyB = useCurrency(currencyIdB) as WrappedTokenInfo


  const { pair } = useDerivedMintInfo(currencyA ?? undefined, currencyB ?? undefined)
  const tabs = useMemo(() => {
    const k = <span onClick={() => setCurrencyTab('kline')}><Trans>K line</Trans></span>;
    const history = <span onClick={() => setCurrencyTab('history')}><Trans>Recent Transactions</Trans></span>;
    const tabs = currencyTab == 'kline' ? <>{k}{history}</> : <>{history}{k}</>;
    return tabs;
  }, [currencyTab]);
  // debugger
  return (<>
    <NetworkAlert />
    <AppBody>
      <Title>
        <div>
          {pair && <DoubleCurrencyLogo currency0={currencyA} currency1={currencyB} size={24} margin={true} />}
          {/* <img src={currencyA?.logoURI} style={{ width: '25px' }}></img>
          <img src={currencyB?.logoURI} style={{ width: '25px', margin: '0 10px 0 -10px' }}></img> */}
          <span style={{ marginLeft: '10px' }}>{currencyA?.symbol}-{currencyB?.symbol}</span>
        </div>
        {/* <span className="right">切换</span> */}
      </Title>
      {pair && <PoolData pair={pair} />}
      {/* <RowComp label="总成交额" valueProps={{ num: 0 }} />
      <RowComp label="总交易数" valueProps={{ num: 0 }} /> */}
      <SubTitle style={{ padding: '1.8rem 1rem 1rem 0rem' }}>
        <span className={currencyTab == 'history' ? 'active' : 'normal'} onClick={() => setCurrencyTab('history')}><Trans>Recent Transactions</Trans></span>
        <span className={currencyTab == 'kline' ? 'active' : 'normal'} onClick={() => setCurrencyTab('kline')}><Trans>K line</Trans></span>
      </SubTitle>
      <div style={{ display: currencyTab != 'kline' ? 'none' : 'block' }}><KChart name={`${currencyA?.symbol}-${currencyB?.symbol}`} /></div>
      <RecentTransactionsDiv style={{ display: currencyTab == 'kline' ? 'none' : 'block' }}><RecentTransactions name={`${currencyA?.symbol}-${currencyB?.symbol}`} /></RecentTransactionsDiv>
    </AppBody>
  </>
  )
}