import { Trans } from '@lingui/macro'
import { NetworkAlert } from 'components/NetworkAlert/NetworkAlert'
import UnsupportedCurrencyFooter from 'components/swap/UnsupportedCurrencyFooter'
import { LOCALE_LABEL } from 'constants/locales'
import { useActiveLocale } from 'hooks/useActiveLocale'
import { useLocationLinkProps } from 'hooks/useLocationLinkProps'
import { useEffect, useState } from 'react'
import { ChevronRight, Code, Globe, Moon, Sun } from 'react-feather'
import { Link, RouteComponentProps } from 'react-router-dom'
import { useDarkModeManager } from 'state/user/hooks'
import styled from 'styled-components/macro'
import { AutoColumn } from '../../components/Column'
import { Wrapper } from '../../components/swap/styleds'
import { useIsSwapUnsupported } from '../../hooks/useIsSwapUnsupported'
import useToggledVersion from '../../hooks/useToggledVersion'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen } from '../../state/application/hooks'
import {
  useDerivedSwapInfo
} from '../../state/swap/hooks'
import AppBody from '../AppBody'

const ToggleLang = styled(Link)`
  text-decoration: none;
  color: ${({ theme }) => theme.text2};
`

const ToggleMenuItem = styled.button`
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: center;
  padding: 0.5rem 0.5rem;
  justify-content: space-between;
  font-size: 1rem;
  font-weight: 500;
  border-bottom:${({ theme }) => `1px solid ${theme.bg2}`};
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }

`
export default function My({ history }: RouteComponentProps) {
  const open = useModalOpen(ApplicationModal.MENU)
  const toggledVersion = useToggledVersion()
  const {
    currencies,
  } = useDerivedSwapInfo(toggledVersion)

  const [menu, setMenu] = useState<'main' | 'lang'>('main')

  useEffect(() => {
    setMenu('main')
  }, [open])

  const swapIsUnsupported = useIsSwapUnsupported(currencies?.INPUT, currencies?.OUTPUT)

  const activeLocale = useActiveLocale()
  const { to, onClick } = useLocationLinkProps(activeLocale == 'en-US' ? 'zh-CN' : 'en-US');
  const [darkMode, toggleDarkMode] = useDarkModeManager()
  return (
    <>
      <NetworkAlert />
      <AppBody>
        <Wrapper id="swap-page">
          <AutoColumn gap={'md'}>
            <ToggleMenuItem>
              <div><Trans>My Node</Trans></div>
              <Globe opacity={0.6} size={16} />
            </ToggleMenuItem>
            <ToggleMenuItem>
              <div><Trans>Code</Trans></div>
              <Code opacity={0.6} size={16} />
            </ToggleMenuItem>
            <ToggleMenuItem onClick={() => history.push('/recentTransactions')}>
              <div><Trans>Recent Transactions</Trans></div>
              <ChevronRight />
            </ToggleMenuItem>
            <ToggleMenuItem>
              <ToggleLang to={to || '#'} onClick={onClick}>
                <Trans>Language</Trans>
              </ToggleLang>
              <ToggleLang to={to || '#'} onClick={onClick}>{LOCALE_LABEL[activeLocale]}</ToggleLang>
            </ToggleMenuItem>
            <ToggleMenuItem onClick={() => toggleDarkMode()}>
              <div>{darkMode ? <Trans>Light Theme</Trans> : <Trans>Dark Theme</Trans>}</div>
              {darkMode ? <Moon opacity={0.6} size={16} /> : <Sun opacity={0.6} size={16} />}
            </ToggleMenuItem>
          </AutoColumn>
        </Wrapper>
      </AppBody>
      {!swapIsUnsupported ? null : (
        <UnsupportedCurrencyFooter show={swapIsUnsupported} currencies={[currencies.INPUT, currencies.OUTPUT]} />
      )}
    </>
  )
}
